import { observer } from 'mobx-react-lite';
import './App.css';
import { useStore } from './context';

const App = observer(() => {
  const store = useStore();
  return (
    <div className="right">
      <h4></h4>
      <h4></h4>
      <h4>cpu</h4>
      <h4></h4>
      <h4></h4>
      {store.currentService?.name}
      {store.currentService?.data &&
        Object.entries(store.currentService.data).map(([key, value]) => (
          <div key={key}>
            {key}: {value}
          </div>
        ))}
    </div>
  );
});

export default App;
